$maincolor: rgb(111, 8, 207);
$color:white;
$font:  'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
$subcolor:rgb(153, 96, 206);


@mixin display($sel){
    display: flex;
    flex-direction: $sel;
    align-items: center;
    justify-content: center;
    
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color:$color;
    overflow: hidden;
    
}

.App{
    background: linear-gradient(rgba(2,2,2,0.7),rgba(2,2,2,0.5)) , url("../images/pexels-drew-williams-2453658.jpg");
    background-size: cover;
    background-position: center center;
    height: 100vh;
    width: 100%;
}

// header styling
header{
    @include display(row);
    justify-content:space-between;
    padding: 10px;

   .left .title{
        font-family: $font;
        text-decoration: none;
        color: $subcolor;
        font-size: 50px;
    }


    .right{
        margin-right: 20px;
    }
    
    .right .link{
        text-decoration: none;
        margin: 0 8px;
        font-family: $font;
        color: $color;
        transition: .5s ease;

        &:hover{
            color: $subcolor;
        }
    }
}

//styling about
.about{
    @include display(column);
    margin-top: 30px;

    h1{
        font-family: $font;
        font-size: 40px;
        margin-bottom: 20px;
    }

    p{
        font-family: calibri;
        width: 500px;
    }
}

// contact style
.contact{
    @include display(column);
    @extend .about;

    .container{
        @include display(column);
        width: 400px;
        background: rgba(2,2,2,0.5);
        padding: 20px 10px;

        .up{
            width: 95%;
            height: 40px;
            border: none;
            border-bottom: 2px solid $maincolor;
            outline: none;
            border-radius: 10px;
            padding-left: 10px;
            margin: 10px 0;
            font-family: $font;
            background: rgba(2,2,2,0.7);
            font-size: 16px;
        }

        textarea{
            @extend .up;
            height: 90px;
            width: 95%;
            border-left: 2px solid $maincolor;
            resize: none;
        }

        .btn{
            width: 95%;
            position: relative;
            height: 45px;
            margin-top: 10px;

            &:hover .overlay{
                height: 100%;
            }

            #btn{
                width: 100%;
                height: 100%;
                font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
                font-size: 20px;
                background: transparent;
                cursor: pointer;
                border-radius: 10px;
                border: 2px solid $maincolor;
                outline: none;
                z-index: 2;
                position: absolute;
            }

            .overlay{
                height: 0;
                width: 100%;
                background: $maincolor;
                top: 0;
                left: 0;
                border-radius: 10px;
                z-index: 1;
                position: absolute;
                transition: .2s ease-out;
            }
        }

  

    }
}

// link styling
.link-container{
    @include display(row);
    margin-top:20px;

    .link{
        text-decoration: none;
        font-family: $font;
        font-size: 15px;
        margin-left: 10px;
        transition: .3s ease;
        padding-bottom: 4px;
        border-bottom: 2px solid transparent;
    }

    .link.active{
       border-color: $maincolor;
       
    }
}

// img styling
.img-container{
    @include display(row);
    flex-wrap: wrap;
    margin: 30px;
    margin-top: 20px;
    height: 300px;
    overflow: auto;
    scroll-snap-type: y mandatory;


    .img-card{
        position: relative;
        width: 400px;
        height: 250px;
        margin: 20px;
        border: 5px solid $maincolor;
        border-radius: 10px;

        img{
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            object-fit: cover;
            border-radius: 10px;
        }
    }
}

// item styling
.item-container{
    @include display(row);
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 20px;

    .img-card{
        position: relative;
        width: 300px;
        height: 200px;
        margin: 20px;
        border: 2px solid $maincolor;
        border-radius: 10px;

        img{
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            object-fit: cover;
           
        }
    }

    .content{
        width: 400px;
        padding: 10px;

        h1{
            font-size: 50px;
            font-family: $font;
        }

        p{
            font-family: calibri;
            margin-top: 5px;
        }
    }
    
}


// footer styling
footer{
    position: absolute;
    bottom: 0;
    background: rgba(2,2,2,0.5);
    width: 100%;
    text-align: center;
    padding: 10px 0;
    font-family: $font;
    font-size: 14px;

}

